import React from "react";
import mapboxgl from "mapbox-gl";

import BuildingDetail from "./BuildingDetail";
import "./PrettyMap.css";

mapboxgl.accessToken =
  "pk.eyJ1Ijoib2xla3NpaXBhc2ljaG55aSIsImEiOiJjaXRvZnN0Z2owMDBoMnRueTl3dDB1OXd3In0.Sog1Iz1Vc1RlMucteIEP_A";

class PrettyMap extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      lng: 18.063918,
      lat: 59.332378,
      zoom: 14.2,
    };
  }

  componentDidMount() {
    const map = new mapboxgl.Map({
      container: this.mapContainer,
      style: "mapbox://styles/oleksiipasichnyi/ck9lmg3q72w671iqop631a03x",
      center: [this.state.lng, this.state.lat],
      zoom: this.state.zoom,
    });

    map.on("move", () => {
      this.setState({
        lng: map.getCenter().lng.toFixed(4),
        lat: map.getCenter().lat.toFixed(4),
        zoom: map.getZoom().toFixed(2),
      });
    });

    map.on("click", "masterfootprints-stockholm", function (e) {
      new mapboxgl.Popup({closeButton: false})
        .setLngLat(e.lngLat)
        .setHTML(
          "<h3>" +
            e.features[0].properties.IdAdr +
            "</h3>" +
            "<p>Energiprestanda: " +
            e.features[0].properties.P_EgiEnergiPrestanda +
            " kWh/mÂ²<br/>Year of construction: " +
            e.features[0].properties.EgenNybyggAr +
            "</p>"
        )
        .addTo(map);
    });

    // Change the cursor to a pointer when the mouse is over the states layer.
    map.on("mouseenter", "masterfootprints-stockholm", function () {
      map.getCanvas().style.cursor = "pointer";
    });

    // Change it back to a pointer when it leaves.
    map.on("mouseleave", "masterfootprints-stockholm", function () {
      map.getCanvas().style.cursor = "";
    });
  }

  render() {
    return (
      <div>
        <div className="sidebar">
          <div>
            Longitude: {this.state.lng} | Latitude: {this.state.lat} | Zoom:{" "}
            {this.state.zoom}
          </div>
        </div>
        <div
          ref={(element) => (this.mapContainer = element)}
          className="mapContainer"
        />
      </div>
    );
  }
}

export default PrettyMap;
